.marketBanner{background-color:var(--bg-color);padding:4rem 2rem;transition:background-color .3s ease}.marketBanner.dark{background-color:var(--dark)}.marketBanner.light{background-color:var(--surface)}.marketBanner .container{display:flex;justify-content:space-between;align-items:center;max-width:1200px;margin:0 auto;flex-wrap:wrap}.marketBanner .textContent{flex:1;min-width:300px}.marketBanner .textContent .title{font-size:2.5rem;font-weight:700;margin-bottom:1rem;color:var(--text-color)}.marketBanner .textContent .description{font-size:1.2rem;color:var(--secondary)}.marketBanner .textContent .description span{font-weight:600}.marketBanner .textContent .description span.dark{color:var(--surface)}.marketBanner .textContent .description span.light{color:var(--dark)}.marketBanner .imageContent{flex:1;min-width:300px;text-align:right}.marketBanner .imageContent .image{max-width:100%;height:auto;border-radius:12px}