.row{width:100%;max-width:1200px;margin:0 auto;padding:0 1rem;display:flex;align-items:center;justify-content:space-between;padding:1rem;border-bottom:1px solid var(--line);font-size:.9rem;gap:1rem}.row.dark{border-bottom:1px solid var(--on-surface)}.row:hover{transform:translateY(-4px);background:hsla(0,0%,100%,.1)}.row.dark:hover{transform:translateY(-4px);background:rgba(47,50,65,.5)}.rank{width:40px;text-align:center}.coin{display:flex;align-items:center;gap:.75rem;min-width:160px}.coin .logo{width:24px;height:24px;border-radius:50%}.coin .nameSymbol{display:flex;align-items:center;gap:8px}.coin .name{font-weight:600}.coin .symbol{border-left:1px solid var(--stroke);padding-left:8px;font-size:.8rem;color:var(--secondary)}.price{width:100px;text-align:right}.change{width:80px;text-align:right;font-weight:600}.green{color:var(--success)}.red{color:var(--critical)}.marketCap{width:140px;text-align:right}.sparkline{width:100px;text-align:center}.tradeButton{flex-shrink:0;padding:.4rem 1rem;border-radius:90px;cursor:pointer;transition:all .3s ease}.tradeButton.light{background-color:var(--white);color:var(--on-surface);border:2px solid var(--stroke)}.tradeButton.dark{background-color:var(--on-surface);color:var(--white);border:2px solid var(--stroke)}.tradeButton.active{background-color:var(--primary);color:var(--white);border:none}