.buttonContainer{display:flex;flex-direction:row;align-items:flex-start;justify-content:flex-start;gap:8px;width:100%;max-width:1140px;margin:20px auto;padding:0 15px}.buttonContainer button{font-size:14px;font-weight:700;line-height:16px;padding:8px 20px;border-radius:24px;cursor:pointer;line-height:16px;border:none}.buttonContainer button.activeButton{background-color:var(--primary);color:var(--white)}.buttonContainer button.inactiveButton{background:none}.buttonContainer button.inactiveButton.light{color:var(--on-surface)}.buttonContainer button.inactiveButton.dark{color:var(--white)}