.marketContainer{max-width:1200px;width:100%;padding:0 1rem;margin:0 auto;display:flex;flex-direction:row;align-items:center;justify-content:space-between;flex-wrap:wrap;gap:1rem;margin-bottom:80px;border-radius:24px}.marketContainer.light{background-color:var(--white)}.marketContainer.dark{background-color:var(--dark2)}.cardContainer{display:flex;flex-direction:row;justify-content:space-between;align-items:center;width:100%;gap:22px;border-top:1px solid;margin-bottom:20px;padding-top:20px}.cardContainer.light{border-color:var(--line)}.cardContainer.dark{border-color:var(--on-surface)}.card{width:calc(25% - 16.5px);padding:20px;border-right:1px solid}.card.light{border-color:var(--line)}.card.dark{border-color:var(--on-surface)}.card:last-child{border-right:none}.topSection{display:flex;flex-direction:row;align-items:center;justify-content:space-between;width:100%;margin-bottom:1rem}.left{display:flex;flex-direction:column;align-items:flex-start;justify-content:center}.left .icon{width:32px;height:32px;margin-bottom:.5rem}.left .name{font-size:1rem;font-weight:600}.right{display:flex;flex-direction:column;align-items:flex-end;justify-content:flex-start;width:50%}.right .volumeChange{font-size:.85rem;margin-top:.5rem;font-weight:500;border-radius:100px;padding:2px 8px;color:var(--white);font-size:16px;font-weight:700;line-height:24px}.right .volumeChange.positive{background-color:var(--success)}.right .volumeChange.negative{background-color:var(--critical)}.priceContainer{display:flex;flex-direction:row;align-items:center;justify-content:space-between;width:100%}.price{text-align:left;font-size:1.1rem;font-weight:600;flex:0 1 auto}.symbol{text-align:right;font-size:1rem;font-weight:400;color:var(--secondary);margin-left:auto}